<template>
  <div class="table-responsive">
    <table class="table mb-0">
      <thead class="thead-default">
      <tr>
        <th>#</th>
        <th></th>
        <th></th>
        <th>ID</th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(value, key, index) in listData">
        <th scope="row">{{value.id}}</th>
        <td>{{value.param.nickname}}</td>
        <td>{{value.param.improve}}</td>
        <td>{{value.param.code}}</td>
        <td>{{value.param.times}}</td>
        <td>{{value.param.age}}</td>
        <td>{{value.param.gender}}</td>
        <td>{{value.param.score}}</td>
        <td>
          <buttom class="btn btn-warning" @click="deleteItem(value.id)"></buttom>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import $ from 'jquery';

  export default {
    name: "list",
    data() {
      return {
        listData: []
      }
    },
    mounted() {
      this.getList();
    },
    methods: {
      deleteItem(id) {
        const _this = this;
        console.log(id)
        $.ajax({
          method: "POST", //  POST  GET 
          url: "https://115.29.201.200/api/del", // 
          data:{
            //GET  URL  processData 
            //  Key/Value jQuery  {foo: ["bar1", "bar2"]}  "&foo=bar1&foo=bar2"
            id,
          },
          success(res) {
            console.log(res);// 
            _this.getList();
          },
          error(e){
            console.log(e)//
          }
        });
      },
      getList() {
        this.listData = [];
        this.$http.get('https://115.29.201.200/api/getList', {}).then(function (response) {
          const arr = response.body.msg;
          console.log('', arr);
          for (let i = 0; i < arr.length; i++) {
            // console.log(JSON.parse(arr[i].param))
            this.listData.push({
              id: arr[i].id,
              param: JSON.parse(arr[i].param)
            })
          }
          console.log(this.listData);
          // response.dataResponseData
        }, function (response) {
          // 
        });
      },
    }
  }
</script>

